.modal__container {
  position: fixed;
  width: 460px;
  height: 312px;
  left: calc(50% - 460px / 2);
  top: calc(50% - 312px / 2 - 0.5px);
  background: #ffffff;
  box-shadow: 0px 8px 32px rgba(0, 0, 0, 0.08);
  border-radius: 12px;

  &_header {
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    margin-top: 20px;
    justify-content: space-between;
    align-items: center;
    width: 396px;

    h1 {
      font-family: "SB Sans Interface";
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 26px;
      color: #333333;
    }

    div {
      position: relative;
      width: 28px;
      height: 28px;
      background: rgba(0, 0, 0, 0.04);
      border-radius: 100px;
    }

    img {
      position: absolute;
      left: 28%;
      top: 28%;
    }
  }

  &_icon {
    position: relative;
    margin: 0 auto;
    margin-top: 50px;
    width: 80px;
    height: 80px;
    background-color: rgba(232, 78, 88, 0.15);
    border-radius: 100px;

    img {
      position: absolute;
      left: 25%;
      top: 25%;
    }
  }

  &_button {
    display: flex;
    width: 396px;
    margin: 0 auto;
    justify-content: flex-end;
    margin-bottom: 32px;

    button {
      width: 88px;
      height: 44px;
    }
  }
}
