.step2__container {
  margin-left: 110px;

  .user-input {
    margin-top: 24px;
  }

  &_checkboxes input {
    width: 16px;
    height: 16px;
    border: 1.5px solid rgba(0, 0, 0, 0.24);
    border-radius: 4px;
  }

  &_radios input {
    width: 20px;
    height: 20px;
    border: 1.5px solid rgba(0, 0, 0, 0.24);
    border-radius: 100px;
  }

  .navigation-buttons {
    display: flex;
    justify-content: space-between;

    :nth-child(2) {
      margin-right: 98px;
    }
  }
}
